.system-audit-pring {
  margin: 0 auto;
  width: 90vw;
  height: 97vh;
}
.system-audit-pring .tg {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
  height: 100%;
}
.system-audit-pring .tg td {
  font-family: Arial, sans-serif;
  font-size: 14px;
  padding: 10px 5px;
  border-style: solid;
  border-width: 1px;
  overflow: hidden;
  word-break: normal;
  border-color: black;
}
.system-audit-pring .tg th {
  font-family: Arial, sans-serif;
  font-size: 14px;
  font-weight: normal;
  padding: 10px 5px;
  border: 0px;
  overflow: hidden;
  word-break: normal;
  height: 3em;
}
.system-audit-pring .tg th h1 {
  padding-top: 1em;
}
.system-audit-pring .tg .tg-qtf5 {
  text-align: left;
}
.system-audit-pring .tg .tg-obcv {
  text-align: center;
}
.system-audit-pring .tg .left1 {
  width: 3em;
  padding: 0 15px;
}
.system-audit-pring .tg .left2 {
  width: 10em;
}
.system-audit-pring .tg .tg-wp8o {
  text-align: center;
  vertical-align: middle;
}
.system-audit-pring .tg .tg-73oq {
  text-align: left;
  vertical-align: top;
}
.system-audit-pring .hfixed {
  height: 2em;
}
.system-audit-pring .h20 {
  height: 20%;
}
.system-audit-pring .w50 {
  width: 40vw;
  height: 100%;
  position: relative;
}
.system-audit-pring .date {
  position: absolute;
  bottom: 0px;
  right: 5px;
}
.system-audit-pring .sign {
  position: absolute;
  bottom: 2em;
  left: 6em;
}
.system-audit-pring .xtsm-content {
  color: red;
  font-size: 12px !important;
}
.system-audit-pring .version {
  text-align: right;
  padding-right: 10px;
}
